const authorQuery = graphql`
  {
    site: allSite {
      edges {
        node {
          siteMetadata {
            hero {
              heading
              maxWidth
            }
          }
        }
      }
    }
  }
`;

const ArticlesHero: React.FC<IAuthor> = ({ authors }) => {
  const { gridLayout = 'tiles', hasSetGridLayout, setGridLayout } = useContext(
    GridLayoutContext,
  );

  const results = useStaticQuery(authorQuery);
  const hero = results.site.edges[0].node.siteMetadata.hero;
  const tilesIsActive = hasSetGridLayout && gridLayout === 'tiles';
  const featuredAuthor = authors.find(author => author.featured);

  if (!featuredAuthor) {
    throw new Error(`
      No featured Author found.
      Please ensure you have at least featured Author.
  `);
  }

  return (
    <Section relative id="Articles__Hero">
      {/* <ViewCountApi><p><span id="visits">0</span></p></ViewCountApi> */}
      <HeadingContainer style={{ maxWidth: `${hero.maxWidth}px` }}>
      
        <HeroHeading dangerouslySetInnerHTML={{ __html: hero.heading }} />
      </HeadingContainer>
      
      <SubheadingContainer>
 
        <Bio author={featuredAuthor} />
        
        <GridControlsContainer>
          <GridButton
            onClick={() => setGridLayout('tiles')}
            active={tilesIsActive}
            data-a11y="false"
            title="Show articles in Tile grid"
            aria-label="Show articles in Tile grid"
          >
            <Icons.Tiles />
          </GridButton>
          <GridButton
            onClick={() => setGridLayout('rows')}
            active={!tilesIsActive}
            data-a11y="false"
            title="Show articles in Row grid"
            aria-label="Show articles in Row grid"
          >
            <Icons.Rows />
          </GridButton>
         <img src="https://neilblaze-spotify-live.vercel.app/api/now-playing" onClick="this.src='https://neilblaze-spotify-live.vercel.app/api/now-playing?rand='+Math.random();"/>
         {/* <iframe id="spotify" width="320" height="52" scrolling="no" frameborder="0" src="https://neilblaze-spotify-live.vercel.app/api/now-playing" allowTransparency="true"/> */}
         {/* <iframe width="320" height="52" scrolling="no" frameborder="0" src="https://neilblaze-spotify-live.vercel.app/api/now-playing" allowTransparency="true"/> */}
         
        </GridControlsContainer> 
      </SubheadingContainer>

    </Section>
  );
};

export default ArticlesHero;

